import React from "react";
import { Link } from "react-router-dom";
import "./App.css";
import Form from "react-bootstrap/Form";

const scalNames = {
  c: "Celsius",
  f: "Fahrenheit",
};

function toCelsius(fahrenheit) {
  return ((fahrenheit - 32) * 5) / 9;
}

function toFahrenheit(celsius) {
  return (celsius * 9) / 5 + 32;
}

function tryConvert(temperature, convert) {
  const input = parseFloat(temperature);
  if (Number.isNaN(input)) {
    return "";
  }
  const output = convert(input);
  const rounded = Math.round(output * 1000) / 1000;
  return rounded.toString();
}

function BoilingVerdict(props) {
  if (props.celsius >= 100) {
    return <p>The water would boil.</p>;
  }
  return <p>The water would not boil.</p>;
}

class TemperatureInput extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.props.onTemperatureChange(e.target.value);
  }

  render() {
    const temperature = this.props.temperature;
    const scale = this.props.scale;
    return (
      <filedset>
        <legend>Enter temperature in {scalNames[scale]}:</legend>
        {/* <input  value={temperature} onChange={this.handleChange} /> */}
        <Form.Control
          size="lg"
          type="text"
          placeholder="temperature"
          value={temperature}
          onChange={this.handleChange}
        />
      </filedset>
    );
  }
}

class Calculator extends React.Component {
  constructor(props) {
    super(props);
    this.handelCelsiusChange = this.handelCelsiusChange.bind(this);
    this.handelFahrenheitChange = this.handelFahrenheitChange.bind(this);
    this.state = { temperature: "", scale: "c" };
  }

  handelCelsiusChange(temperature) {
    this.setState({ scale: "c", temperature });
  }

  handelFahrenheitChange(temperature) {
    this.setState({ scale: "f", temperature });
  }

  render() {
    const scale = this.state.scale;
    const temperature = this.state.temperature;
    const celsius =
      scale === "f" ? tryConvert(temperature, toCelsius) : temperature;
    const fahrenheit =
      scale === "c" ? tryConvert(temperature, toFahrenheit) : temperature;
    return (
      <div className="App">
        <header className="App-header">
          <TemperatureInput
            scale="c"
            temperature={celsius}
            onTemperatureChange={this.handelCelsiusChange}
          />
          <br />
          <TemperatureInput
            scale="f"
            temperature={fahrenheit}
            onTemperatureChange={this.handelFahrenheitChange}
          />

          <BoilingVerdict celsius={parseFloat(celsius)} />

          <Link className="App-link"  to="/">
            counter
          </Link>
        </header>
      </div>
    );
  }
}

export default Calculator;
